@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@100;200;300;400;500;600;700&display=swap);
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Work Sans', sans-serif;
}

input {
    display: block;
}

form{
    width: 100%;
    max-width: 500px;
    margin: 0 auto;
    margin-top: 60px;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    padding: 20px;
    border-radius: 5px;
}

form input,textarea{
    border: none;
    outline: none;
    background-color: rgb(233, 188, 213);
    padding: 10px;
    border-radius: 5px;
    margin: 10px 0;
    width: 100%;
}

form button{
    padding: 10px 20px;
    width: 100%;
    background-color: rgb(222, 14, 128) ;
    color: white;
    border-radius: 5px;
    border: none;
    cursor: pointer;
}

a{
    text-decoration: none;
    color: rgb(150, 35, 95);
}
#root{
    width: 100%;
    margin: 0 auto;
}
header{
    padding: 10px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    }
    
    header a {
        text-decoration: none;
        color: rgb(190, 33, 106);
        font-size: 17px;
    }
    .logo{
        display: flex;
