    }
    
    .active{
        font-weight: bold ;
    }
    
    .greet{
        color: rgb(190, 33, 106);
        margin-left: 20px;
        cursor: pointer;
    }
    
    .user-info{
        position: relative;
        display: inline-block;
    }
