        justify-content: center;
        align-items: center;
        cursor: pointer;
        position: relative;
    }
    .carticon,.spicon{
        font-size: 25px;
        margin-left: 5px;
        color:rgb(190, 33, 106) ;
    }
    .cart .totalcart{
        width: 20px;
        height: 20px;
