.dogitem{
    margin: 20px;
    display: flex;
    padding: 20px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.dogitem img{
    width: 40%;
    height: auto;
}
.dogiteminfor{
    margin: 0 50px;
}
.note{
    margin: 20px;
    padding: 20px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}
.dogiteminfor h2{
    padding-bottom: 20px;
    color:rgb(190, 33, 106) ;
}
.dogiteminfor ul{
    margin-bottom: 20px;
}